.header {
  min-height: 100vh;
  // padding-top: 40px;
  background-blend-mode: overlay;
  background-image: url(../img/travel_bg.png);
  background-color: #c7bda8;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  @media screen and (min-width: 1024px) {
    min-height: 700px;
    background-position: 100% 80%;
  }
}

.header__content {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 1024px) {
    min-height: 700px;
  }
}

.header-bundle {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  // flex-direction: row;
  // align-items: flex-start;
  // justify-content: space-between;
  padding: 22px 0 18px;
}

.header__logo {
  &-text {
    font-family: "Quite Chocolatey", sans-serif;
    font-size: 2.8em; //44.8px
    line-height: 1em; //44.8px
    // font-size: 5.5em; //88px
    // line-height: 2em; //32px
    color: #cf415d;
    text-decoration: none;
    @media screen and (min-width: 1024px) {
      font-size: 5.5em;
    }
  }
}

.burger {
  &-toggle {
    display: none;
    &:checked ~ .nav__list {
      display: block;
    }
  }
  &-view {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 26px;
    height: 21px;
    @media screen and (min-width: 768px) {
      display: none;
    }
  }
  &-line,
  &-view::before,
  &-view::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: #fff;
    border-radius: 3px;
  }
}

.nav {
  &__list {
    display: none;
    position: absolute;
    min-width: 290px;
    background-color: #c7bda8;
    border: 3px solid #d03958;
    list-style-type: none;
    bottom: 0;
    left: 50%;
    z-index: 999;
    transform: translateY(100%) translateX(-50%);
    @media screen and (min-width: 768px) {
      display: flex;
      position: static;
      justify-content: space-between;
      background-color: unset;
      transform: unset;
      border: none;
    }
    @media screen and (min-width: 1024px) {
    }
  }
  &__item {
    display: flex;
    padding: 17px 0;
    position: relative;
    &:not(:last-child) {
      border-bottom: 3px solid #d03958;
      @media screen and (min-width: 768px) {
        border: 0;
      }
    }
    @media screen and (min-width: 768px) {
      padding: 0;
    }
    @media screen and (min-width: 1024px) {
      
    }
  }
  &__link {
    font-family: "Montserrat Regular", sans-serif;
    font-size: 1.625em; //26px
    line-height: 1em;
    font-weight: 600;
    color: #1b1b1b;
    text-decoration: none;
    text-transform: uppercase;
    padding: 10px 20px 10px 40px;
    &:hover {
      background-color: #d03958;
      color: #fff;
      border-radius: 5px/5px;
    }
    @media screen and (min-width: 768px) {
      padding: 10px 15px 10px 15px;
      font-size: 1.1em;
      line-height: 1.4em;
      &:hover {
        background-color: #d03958;
        color: #fff;
        border-radius: 5px/5px;
      }
    }
    @media screen and (min-width: 1200px) {
      padding: 12px 15px;
    }
  }
}

.trevel__content {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  &-box {
    max-width: 500px;
    display: flex;
    flex-direction: column;
  }
  &-head {
    min-width: 350px;
    font-family: "Montserrat Bold", sans-serif;
    font-size: 3em;
    line-height: 1em;
    text-transform: uppercase;
    color: #d03958;
    @media screen and (min-width: 1024px) {
      font-size: 5.6em;
      line-height: 1em;
    }
  }
  &-text {
    max-width: 340px;
    font-size: 1em;
    line-height: 1em;
    color: black;
    margin-top: 10px;
    @media screen and (min-width: 768px) {
      font-size: 1.3em; //20px
      line-height: 1em;
    }
  }
  @media screen and (min-width: 768px) {
    align-items: center;
  }
  @media screen and (min-width: 1024px) {
    flex-direction: row;
    justify-content: space-evenly;
    flex-grow: 1;
  }
}

.head__color {
  color: black;
}

.trevel__form-item {
  display: flex;
  position: relative;
  left: 10px;
  @media screen and (min-width: 1024px) {
    width: 400px;
    height: 275px;
  }
}

.form__checkbox {
  display: none;
  &:checked ~ .trevel__form {
    display: block;
    @media screen and (min-width: 1024px) {
      display: none;
    }
  }
}

.form__button-request {
  font-family: "Montserrat Regular", sans-serif;
  border-radius: 5px;
  padding: 10px 10px;
  background-color: #d03958;
  color: #fff;
  margin-top: 15px;
  @media screen and (min-width: 1024px) {
    display: none;
  }
}

.trevel__form {
  display: none;
  width: 350px;
  min-height: 275px;
  padding: 34px 22px 22px 24px;
  background-color: #d03958;
  border-radius: 10px;
  position: absolute;
  right: 3px;
  top: -200px;
  @media screen and (min-width: 425px) {
    right: 0;
    left: 5px;

  }
  @media screen and (min-width: 1024px) {
    display: flex;
    flex-direction: column;
    top: 0;
  }
}

.trevel__form-registration {
  margin-top: 20px;
}

.close__form {
  font-family: "Montserrat Bold", sans-serif;
  font-size: 14px;
  color: rgb(46, 44, 44);
  position: absolute;
  top: 10px;
  right: 10px;
  text-decoration: none;
  @media screen and (min-width: 1024px) {
    display: none;
  }
}

.trevel__form-head {
  font-family: "Montserrat Regular", sans-serif;
  font-size: 22px;
  text-transform: uppercase;
  color: #fff;
  margin-left: 40px;
}

.travel__form-input {
  width: 100%;
  min-height: 57px;
  font-size: 20px;
  line-height: 25px;
  padding-left: 20px;
  border: none;
  border-radius: 5px;
}

.travel__form-input:not(:first-child) {
  margin-top: 20px;
}

.accept__terms-box {
  margin-top: 20px;
  &-checked {
    width: 18px;
    height: 18px;
    border: none;
    border-radius: 5px;
  }
  &-text {
    font-size: 20px;
    line-height: 25px;
    color: #fff;
  }
}

.travel__form-button {
  font-family: "Montserrat Regular", sans-serif;
  width: 100%;
  min-height: 57px;
  margin-top: 20px;
  background-color: #161616;
  border: none;
  border-radius: 5px;
  font-size: 1.4em;
  color: #fff;
  text-transform: uppercase;
  justify-content: center;
  &:hover {
    background-color: #942b3e;
    cursor: pointer;
  }
}
