.our__services-container {
  padding: 40px 0 70px 0;
}

.our__services-content {
  min-height: 380px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.our__services-box-head {
  display: flex;
  font-family: "Couture Bold", sans-serif;
  font-size: 2em; //32px
  line-height: 1em; //32px
  text-transform: uppercase;
  margin-bottom: 40px;
  &-color {
    color: #d03958;
  }
  @media screen and (min-width: 1024px) {
    font-size: 3.1em;
  }
}

.our__services-topics-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
}

.our__services-topics {
  display: flex;
  max-width: 180px;
  min-height: 230px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  &-text {
    // margin-top: 20px;
    font-size: 1.125em; //20px
    line-height: 1.25em; //28px
    font-weight: 400;
    color: #161616;
  }
}
.our__services-topics:not(:last-child) {
  margin-bottom: 40px;
  @media screen and (min-width: 768px) {
    margin: 0;
  }
}

.story__container {
  background-color: #d03958;
}

.story__content {
  // min-height: 120px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  &-text {
    width: 100%;
    // width: 630px;
    font-size: 1.125em; //18px
    line-height: 1.25em; //20px
    // font-size: 20px;
    // line-height: 28px;
    color: #fff;
    @media screen and (min-width: 1024px) {
      width: 630px;
    }
  }
  &-button {
    font-family: "Montserrat Regular", sans-serif;
    font-size: 1.125em; //18px
    line-height: 1.25em; //20px
    // font-size: 22px;
    // line-height: 28px;
    color: #161616;
    text-transform: uppercase;
    background-color: #e3d5b8;
    padding: 10px 20px;
    border: none;
    margin-top: 20px;
    @media screen and (min-width: 1024px) {
      font-size: 1.4em;
      padding: 20px 60px;
      border: 1px solid #e3d5b8;
    }
  }
  @media screen and (min-width: 1024px) {
    min-height: 120px;
    flex-wrap: nowrap;
    justify-content: space-evenly;
    align-items: center;
  }
}

.pricing__container {
  padding-top: 30px;
  background-color: #e3d5b8;
}

.pricing__content {
  // min-height: 480px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  &-head {
    font-family: "Montserrat Bold", sans-serif;
    font-size: 2em;
    line-height: 1em;
    text-transform: uppercase;
    margin-bottom: 30px;
    @media screen and (min-width: 1024px) {
      font-size: 3.1em;
    }
  }
  @media screen and (min-width: 1024px) {
    padding-bottom: 0;
  }
}

.pricing__box {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
}

.signup__plan {
  padding-bottom: 10px;
  width: 100%;
  min-width: 240px;
  max-width: 320px;
  display: flex;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  &-name {
    padding-top: 35px;
    font-size: 1.4em;
    line-height: 1.8em;
    color: #161616;
  }
  &-cost {
    font-family: "Montserrat Bold", sans-serif;
    font-size: 3.8em;
    color: #d03958;
    text-transform: uppercase;
  }
  &-info {
    font-size: 1.3em;
    line-height: 1.8em;
    color: #161616;
  }
  &-button {
    font-family: "Montserrat Regular", sans-serif;
    font-size: 1.125em; //18px
    line-height: 1.25em; //20px
    // font-size: 22px;
    // line-height: 28px;
    color: #161616;
    text-transform: uppercase;
    padding: 10px 80px;
    background-color: #e3d5b8;
    border: none;
    border-radius: 5px;
    margin-top: 25px;
    &:hover {
      cursor: pointer;
      color: #fff;
      background-color: #d03958;
    }
    @media screen and (min-width: 1024px) {
      font-size: 1.4em;
      line-height: 1.8em;
    }
  }

  &-button:hover {
    cursor: pointer;
    color: #fff;
    background-color: #d03958;
  }
  @media screen and (min-width: 1024px) {
    padding-bottom: 0;
  }
}

.signup__plan:not(:last-child) {
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    margin: 0 10px 0 0;
  }
}

.story__content-button:hover {
  cursor: pointer;
  color: #fff;
  border: 1px solid;
}

.popular__triangle {
  font-size: 16px;
  position: absolute;
  top: 20px;
  left: 0;
  transform: rotate(-45deg);
}

.signup__plan:nth-child(2)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(to left bottom, transparent 50%, #b4a993 0);
  width: 90px;
  height: 90px;
  transform: rotate(90deg);
}

.testi__gallery-container {
  background-color: #fff;
  padding: 30px 0 30px 0;
  @media screen and (min-width: 1024px) {
    padding: 140px 0 120px 0;
  }
}

.testi__gallery-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  @media screen and (min-width: 1024px) {
    flex-direction: row;
    align-items: unset;
  }
}

.testi {
  width: 280px;
  // width: 470px;
  display: flex;
  flex-direction: column;
  &__head {
    font-family: "Montserrat Bold", sans-serif;
    font-size: 2em; //32px
    line-height: 1em; //32px
    color: #1f1f1f;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 20px;
    @media screen and (min-width: 1024px) {
      font-size: 3.1em;
      width: 470px;
    }
  }
}

.testi__block {
  display: flex;
  flex-direction: column;
  width: 280px;
  // margin-bottom: 10px;
  position: relative;
  // margin-bottom: 20px;
  &-text {
    width: 200px;
    font-size: 20px;
    line-height: 28px;
    color: #161616;
    @media screen and (min-width: 1024px) {
      width: 360px;
    }
  }
  &-client {
    font-family: "Montserrat Regular", sans-serif;
    font-size: 20px;
    line-height: 28px;
    color: #d03958;
    margin-top: 20px;
  }
  &:hover {
    background-color: #e3d5b8;
  }
  @media screen and (min-width: 1024px) {
    width: 440px;
    padding: 20px 0 20px 20px;
  }
}

.testi__block:not(:last-child) {
  margin-bottom: 20px;
}

.testi__block-icon {
  position: absolute;
  right: -20px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: -4px -9px 15px rgba(219, 213, 201, 0.9);
  @media screen and (min-width: 1024px) {
    right: -50px;
  }
}

.testi__block-icon-img {
  position: absolute;
  left: -70px;
  border: none;
}

.women__icon {
  left: -14px;
}

.gallery {
  width: 280px;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  &__head {
    font-family: "Montserrat Bold", sans-serif;
    font-size: 2em; //32px
    line-height: 1em; //32px
    color: #d03958;
    text-transform: uppercase;
    text-align: center;
    @media screen and (min-width: 1024px) {
      font-size: 3.1em;
    }
  }
  &__pictures {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 20px;
    margin-top: 30px;
  }
  @media screen and (min-width: 1024px) {
    width: 390px;
    margin-top: 0;
  }
}

.gallery__pictures-icon {
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: -4px -9px 15px rgba(219, 213, 201, 0.9);
}

.gallery__pictures-icon-women {
  position: absolute;
  right: -35px;
  top: -5px;
  border: none;
}

.gallery__pictures-icon-berries {
  position: absolute;
  left: -60px;
  top: -50px;
  border: none;
}

.gallery__pictures-icon-lemon {
  position: absolute;
  left: -20px;
  top: -20px;
  border: none;
}

.gallery__pictures-icon-cup {
  position: absolute;
  right: -25px;
  bottom: 0;
  border: none;
}

.gallery__pictures-icon-legs {
  position: absolute;
  top: -15px;
  left: -10px;
  border: none;
}

.gallery__pictures-icon-rock {
  position: absolute;
  top: -140px;
  left: -200px;
  border: none;
}

.gallery__link {
  font-family: "Montserrat Regular", sans-serif;
  font-size: 1em;
  line-height: 1em;
  // font-size: 20px;
  // line-height: 28px;
  text-transform: uppercase;
  color: #d03958;
  text-align: center;
  margin-top: 46px;
  @media screen and (min-width: 1024px) {
    font-size: 1.3em;
  }
}

.footer__container {
  padding: 20px 0 20px 0;
  background-color: #161616;
  @media screen and (min-width: 1024px) {
    padding: 80px 0 60px 0;
  }
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  &__copyright {
    width: 150px;
    font-size: 1em;
    line-height: 1em;
    // font-size: 20px;
    // line-height: 22px;
    color: #fff;
    @media screen and (min-width: 1024px) {
      font-size: 1.3em;
      width: unset;
    }
  }
}

.footer__text-color {
  color: #d03958;
}
