* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 16px;
  font-family: "PT Serif Regular";
}

.container {
  width: 100%;
}

.content {
  // width: 1010px;
  padding: 0 20px;
  @media screen and (min-width: 768px) {
    padding: 0 15px;
  }
  @media screen and (min-width: 1024px) {
    padding: 0 30px;
  }
  @media screen and (min-width: 1440px) {
    width: 1010px;
    margin: 0 auto;
    padding: 0;
  }
}

@import "./base.scss";
@import "./header.scss";
@import "./main.scss";
